import { Message } from 'element-ui';
// 
var errorMsg = function(msg) {
  Message({
    showClose: true,
    message: msg,
    type: 'error',
    duration: 3000
  })
}
// 
var successMsg = function($msg) {
  $msg = $msg || ''
  Message({
    type: 'success',
    message: $msg
  })
}
export default {
    baseUrl:'http://rb.local.com',
    // baseUrl:'http://rb.ironxu.com',
    errorMsg,
    successMsg
}


